html {
  font-weight: 100;
  margin: 0;
  padding: 0;
  height: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  /* height: 100%; */
}

p {
  margin: 0;
  padding: 0;
}

.RankingBarContainer {
  margin: 5px;
  height: 30px;
}

.RankingBarContainer > span {
  line-height: 30px;
  vertical-align: top;
}

.addPageTextInput {
  margin: 10px;
  width: 90%;
  line-height: 30px;
}

/* #react-root {
  height: 90%;
} */

.MiddleContainer {
  display: table-cell;
  vertical-align: middle;
}

.QRMarkContainer {
  border: solid 1px darkgray;
  padding: 10px;
}

.QRMarkHeader {
  background-color: #66CCFF;
  color: white;
  text-align: center;
}

.QRMarkNumber {
  color: #0080FF;
}

.QRMarkTitle {
  color: orange;
  /* text-shadow: 1px 1px 0 #444; */
  font-weight: bold;
}

.QRMarkBorder {
  border-radius: 5px;
  border: solid 3px #0080FF;
}

.arrow{
  position: relative;
  display: inline-block;
  padding: 0 0 0 30px;
  color: #000;
  vertical-align: middle;
  text-decoration: none;
  font-size: 30px;
}
.arrow::before,
.arrow::after{
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  margin: auto;
  content: "";
  vertical-align: middle;
}

.sample3-2::before{
  top: -10px;
  left: 24px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  width: 26px;
  height: 26px;
  border: 26px solid transparent;
  border-left: 26px solid orange;
}
.sample3-2::after{
  top: -10px;
  left: 0;
  width: 24px;
  height: 26px;
  border-left: 24px solid orange;
}



